export default {
	data() {
		return {
			userName: '',
			userPass: ''
		};
	},
	onLoad() {},
	methods: {
		getUserName: function(event){
			this.userName = event.target.value;
		},
		getUserPass: function(event){
			this.userPass = event.target.value;
		},
		login: function(){
			uni.redirectTo({
				url: '../info/info?userName=' + this.userName + '&userPass=' + this.userPass
			});
		}
	}
};
